import React, { Component } from 'react'
import { NavLink } from 'react-router-dom';
import { Pane, Heading, Button, Dialog, Paragraph } from 'evergreen-ui'
import ConfigService from '../@services/Config';

class PageOverview extends Component {
  constructor(props)
  {
    super(props)
    this.state = {
      busy: false,
      isEditShown: false,
      isDeleteShown: false
    }
  }
  menuStruct = [
    {label:'Config', key:'config'},
    {label:'Tools', key:'tools'},
    {label:'Templates', key:'templates'}
  ]
  render() {
    return (
      <article>
        <Pane display="flex" padding=".5em" background="tint2">
          <Pane flex={1} alignItems="center" display="flex">
            <Heading size={600}>{this.props.currentSite.name} / Settings</Heading>
          </Pane>
          <Pane>
            <Button appearance="minimal" intent="danger" onClick={(e) => { e.preventDefault(); this.setState({ isDeleteShown: true }) }}>Delete Site</Button>
          </Pane>
        </Pane>
        <Pane display="flex" flexWrap="wrap">
          {
            this.menuStruct.map((item, index) => (
              <Pane className="griditem__block" key={index} flex="0 0 calc(25% - 2em)" borderRadius={5} height={128} display="flex" justifyContent="center" alignItems="center" margin="1em" elevation={1} border="default">
                <NavLink to={`/${this.props.match.params.siteKey}/${item.key}`}>
                  <Heading size={500}>{item.label}</Heading>
                </NavLink>
              </Pane>
            ))
          }
        </Pane>
        <Pane padding="1em">
          <Heading size={400}>What is this?</Heading>
          <Paragraph size={400} color="muted">
            Lorem ipsum dolor sit amet, consectetur adipiscing elit. Nam laoreet non augue et faucibus. Ut arcu tellus, hendrerit id sem in, iaculis varius ipsum. Class aptent taciti sociosqu ad litora torquent per conubia nostra, per inceptos himenaeos. Etiam ac aliquet orci. Donec porta risus at nisi placerat pellentesque. Vestibulum ante ipsum primis in faucibus orci luctus et ultrices posuere cubilia curae; Fusce interdum ligula ut cursus egestas. Praesent porttitor non nisl quis sodales. Maecenas efficitur, nibh ac finibus vestibulum, arcu ex tempus nibh, eu convallis lacus diam id elit. Pellentesque elementum ac nibh mattis molestie. Quisque urna turpis, viverra vel tortor sit amet, tincidunt condimentum arcu. Pellentesque luctus aliquam consequat. Vivamus condimentum non risus eget venenatis.
          </Paragraph>
        </Pane>
        <Dialog
          isShown={this.state.isDeleteShown}
          hasHeader={false}
          intent="danger"
          isConfirmLoading={this.state.busy}
          onConfirm={() => {
            this.setState({ busy: true })
            ConfigService.deleteSite(this.props.currentSite.key)
              .then(()=>{
                this.setState({ busy: false, isDeleteShown: false })
                window.location.href="/"
              })
          }}
          onCloseComplete={() => this.setState({ isDeleteShown: false, busy: false })}
          confirmLabel="Delete"
        >
          <Heading size={600}>Are you sure?</Heading>
          <Paragraph size={400} color="muted">
            Removing this {this.props.schemaPath} may effect other components that require it.
          </Paragraph>
        </Dialog>
      </article>
    )
  }
}

export default PageOverview;