import React, { Component } from 'react'
import { Button, Pane, PlusIcon, LinkIcon, SideSheet, FloppyDiskIcon, Heading } from 'evergreen-ui'
import TableCmp from './TableCmp';
import Form from './Form';
import Busy from './Busy';
import ConfigService from '../@services/Config';

class FormRepeater extends Component {
  constructor(props)
  {
    super(props)
    
    this.state = {
      defaultValue: [],
      showAddRow: false,
      isNewField: true,
      saveBusy: false,
      fieldData: {
        [this.props.field.key]: this.props.field
      },
      editField: null
    }
    this.handleChange = this.handleChange.bind(this)
    this.rowSelect = this.rowSelect.bind(this)
    this.updateField = this.updateField.bind(this)
  }

  componentDidMount()
  {
    this.mounted = true
  }
  
  handleChange(key, value)
  {
    this.setState({
      editField: {
        ...this.state.editField,
        [key]: {
          ...this.state.editField[key],
          value: value
        } 
      }
    })
    this.updateField = this.updateField.bind(this)
    this.transpileValues = this.transpileValues.bind(this)
  }

  rowSelect(row, isNew)
  {
    // console.log(row)
    // var row = this.props.field
    this.setState({
      showAddRow: true,
      isNewField: isNew || false,
      editField: false
    })

    ConfigService.getSchema(this.props.field.repeater)
      .then(({ documentation, schema, supportsTemplates, table }) => {

        if (!this.mounted) return;

        var SchemaFormValue = {}

        Object.keys(schema).forEach(key => {

          SchemaFormValue[key] = {
            ...schema[key],
            key: key,
            errors: [],
            onChange: (key, value) => this.handleChange(key, value),
            value: (() => {
              
              if (!(key in row) || row[key] === undefined || isNew) return schema[key].default || undefined;

              switch (schema[key]?.type)
              {
                case 'string[]':
                  return row[key].map(item => { return typeof item === "string" ? (item) : (`${item.id}@${item.version}`) });
                case 'keys':
                  return (() => {
                    var Values = {}
                    Object.keys(schema[key].keys).forEach(k => {
                      var Value = (k in row[key]) ? row[key][k] : (schema[key][k]?.default);
                      if (undefined !== Value) Values[k] = Value;
                    })
                    return Values
                  })();
                default:
                  return row[key];
              }

            })()
          }

        })

        this.setState({
          editField: SchemaFormValue
        })
        
      })
  }

  transpileValues(formData)
  {
    return Object.keys(formData).map(fieldKey => {
      return formData[fieldKey].value
    })
  }

  updateField(e)
  {
    e.preventDefault();

    // TODO: handle "key" changes

    var _value = {};
    
    Object.keys(this.state.editField).forEach(key => {
      if (undefined !== this.state.editField[key].value)
        _value[key] = this.state.editField[key].value;
    })

    var _valueArray = this.state.fieldData[this.props.field.key].value.map(item => {
      var modifier = item.key === _value.key
      return modifier ? _value : item;
    })

    if (this.state.isNewField) {

      _valueArray.push(_value)

    }

    console.log("GOGOGOG", _valueArray)

    this.props.field.onChange(this.props.field.key, _valueArray)

    this.setState({
      showAddRow: false
    })
  }

  render() {
    return (
      <div className="form-block__field">
        <Pane display="flex" justifyContent="space-between" borderBottom="muted">
          <Pane>
            <label className="form-block__field-label">{
              this.props.field.key.startsWith("@")?(
                <LinkIcon size={10} color="selected" marginRight=".5em"/>
              ):null
            }{this.props.field.label}</label>
            {(this.props.field.description?(
              <small className="form-block__field-describe">{this.props.field.description}</small>
            ):null)}
          </Pane>
          <Pane marginBottom=".5em">
            <Button appearance="minimal" iconBefore={PlusIcon} onClick={(e) => { e.preventDefault(); this.rowSelect({}, true); }}>
              Add {this.props.field.label}
            </Button>
          </Pane>
        </Pane>
        <Pane>
          <TableCmp emptyMsg={`No ${this.props.field.label}`} cols={this.props.field?.columns} rows={this.props.field.value || []} onSelect={this.rowSelect}/>
        </Pane>
        <SideSheet
          isShown={this.state.showAddRow}
          onCloseComplete={() => this.setState({ showAddRow: false })}
        >
          {this.state.editField?(
            <Form className="split-content__form" data={this.state.editField}>
              <Pane display="flex" padding="1em" background="tint2" borderBottom="muted">
                <Pane flex={1} alignItems="center" display="flex">
                  <Heading size={600}>{`${this.state.isNewField?'Add':'Edit'} ${this.props.field.label}`}</Heading>
                </Pane>
                <Pane>
                  <Button appearance="minimal" isLoading={this.state.saveBusy} iconBefore={this.state.saveBusy?(null):(FloppyDiskIcon)} onClick={this.updateField}>{this.state.isNewField?'Save':'Update'}</Button>
                </Pane>
              </Pane>
            </Form>
          ):(<Busy/>)}
          {this.state.saveBusy?(<div className="content-busy"></div>):null}
        </SideSheet>
      </div>
    )
  }
}

export default FormRepeater;
